import './reg.css';
import React, { useState } from 'react';
import axios from 'axios';
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';
import { useNavigate } from 'react-router-dom';

const Reg = () => {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [firstName, setFirstName] = useState('');
  const [lastName, setLastName] = useState('');
  const [gender, setGender] = useState('');
  const [birthday, setBirthday] = useState(null);
  const [region, setRegion] = useState('');
  const [registrationStatus, setRegistrationStatus] = useState('');
  const navigate = useNavigate();

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    const userData = {
      username,
      password,
      firstName,
      lastName,
      gender,
      birthday,
      region
    };

    try {
      const response = await axios.post('http://morderboy.ru/api/registration', userData);

      if (response.status === 200) {
        setRegistrationStatus('Ok');
        navigate('/login');
      } else {
        setRegistrationStatus('Bad Request');
      }
    } catch (error) {
      console.error(error);
      setRegistrationStatus('Error');
    }
  };

  return (
    <div className='registration-container'>
      <h1></h1>
      {registrationStatus === 'Ok' && <p>  </p>}
      {registrationStatus === 'Bad Request' && <p>     </p>}
      {registrationStatus === 'Error' && <p>      </p>}<br/>
      <form onSubmit={handleSubmit}>
        <input type="text" value={username} placeholder=" " onChange={(e) => setUsername(e.target.value)} />
       
        <input type="password" value={password} placeholder="" onChange={(e) => setPassword(e.target.value)} />
  
        <input type="text" value={firstName} placeholder="" onChange={(e) => setFirstName(e.target.value)} />
   
        <input type="text" value={lastName} placeholder="" onChange={(e) => setLastName(e.target.value)} />

        <input type="text" value={region} placeholder="" onChange={(e) => setRegion(e.target.value)} />

        <DatePicker selected={birthday} onChange={date => setBirthday(date)} className='dat' placeholderText=" " />
 
        <select value={gender} className='gen' onChange={(e) => setGender(e.target.value)}>
          <option value=""> </option>
          <option value="male"></option>
          <option value="female"></option>
        </select>
 
        <button type="submit"></button>
      </form>
    </div>
  );
};

export default Reg;